import { Component, OnInit } from '@angular/core';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { PayrollTandaTanganService } from 'src/app/sdk/payroll/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';
import { Tanda_tangan } from 'src/app/sdk/payroll/models';
import { FormControl, Validators } from '@angular/forms';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private payrollTandaTanganService: PayrollTandaTanganService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private singleUploadService: SingleUploadService,
    ) { }

    id : string = ''
    data: Tanda_tangan = {};
    toogleUpload = false
    ngOnInit() {
        this.id = this.activatedRoute.snapshot.paramMap.get('id')
        this.form();
        this.getData();

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'tanda_tangan.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_document: data })
                    this.simpan()
                }
            }
        )
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }

    getData(){
        this.payrollTandaTanganService.getPayrollTandaTangansId(this.id).subscribe(
            data=> {
                this.dataForm.setValue(data.data)

                this.singleUploadService.param.next({
                    for_module: "tanda_tangan",
                    for_table: "erp_payroll.tanda_tangan",
                    for_module_id: data.data.id_tanda_tangan
                })
            }
        )
    }


    dataForm: FormGroupTypeSafe<Tanda_tangan>;
    form() {
        this.dataForm = this.fb.group<Tanda_tangan>(
            {
                tanda_tangan: new FormControl('', [Validators.required]),
                jenis: new FormControl('', [Validators.required]),
                jenis_formulir: new FormControl(''),
                judul: new FormControl(''),
                npwp: new FormControl(''),
                jabatan: new FormControl(''),
                id_document: new FormControl(''),
                id_tanda_tangan: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),

            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.data = this.dataForm.value;
        this.payrollTandaTanganService.putPayrollTandaTangansId({id: this.id, body: this.data}).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/payroll/module/pengaturan/tanda-tangan/list'])
    }

}
